import React, { useState } from 'react';
import { Button, Box, TextField, Typography } from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';

import { selectorNameTeachers } from '../../redux/selector';
import { actionsTeacher } from '../Teacher/TeacherSlice';
import { listCourse } from '../../data';
import SelectValue from '../../features/SelectValue';
import ChooseColor from '../../features/ChooseColor';
import { actionsUser } from '../Account/AccountSlice';
const defaultValues = {
    name: '',
    gender: '',
    color: '',
    course: '',
};
function FormRegister() {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [formValues, setFormValues] = useState(defaultValues);
    const nameTeachers = useSelector(selectorNameTeachers);
    const handleChangeForm = (e) => {
        const { name, value } = e.target;
        // Cp nhp gi tr ca tng phn trong form khi ngi dng nhp
        setFormValues({
            ...formValues,
            [name]: value,
        });
    };
    // X l ng k
    const handleSubmit = (e) => {
        e.preventDefault();
        // Ly tng gi tr form
        const { name, gender, course } = formValues;
        let validateForm = true;
        // Kim tra ngi dng c nhp tn hay khng
        if (!name) {
            toast.error('Vui lng nhp tn');
            validateForm = false;
        }
        // Kim tra ngi dng c chn gii tnh hay khng

        if (!gender) {
            toast.error('Vui lng chn gii tnh');
            validateForm = false;
        }
        // Kim tra ngi dng c chn gii mn hc hay khng

        if (!course) {
            toast.error('Vui lng chn mn hc');
            validateForm = false;
        }
        // Kim tra ngi dng c trng vi nhng gio vin  ng k hay khng
        if (nameTeachers.includes(formValues.name)) {
            toast.error('Tn ny  tn ti vui lng nhp tn khc');
            validateForm = false;
        }
        // Nu khng c li th ng k thng cng
        if (validateForm) {
            navigate('/dashboard');
            // gi actions ln redux  thm mt gio vin mi
            dispatch(actionsTeacher.addTeacher({ ...formValues, lessonRegister: [] }));
            // Gi actions ln redux  thay i ti khon ang ng nhp

            dispatch(actionsUser.change(formValues));
        }
    };
    return (
        <form style={{ width: '100%' }} onSubmit={handleSubmit}>
            <Box alignItems="center" justifyContent="center" flexDirection="column" width="100%">
                <Box backgroundColor="#fff" borderRadius="4px" width="100%" mb={2}>
                    <TextField
                        fullWidth
                        id="name-input"
                        name="name"
                        label="H v tn, VD: Nguyn Ch Tnh"
                        type="text"
                        value={formValues.name}
                        onChange={handleChangeForm}
                    />
                </Box>
                {/*  chn gii tnh */}
                <SelectValue
                    handleOnChange={handleChangeForm}
                    data={['Nam', 'N', 'Khc']}
                    label="Gii tnh"
                    name="gender"
                />
                {/*  chn mn hc */}
                <SelectValue handleOnChange={handleChangeForm} data={listCourse.sort()} label="Mn hc" name="course" />

                {/* Ni chn mu cho mnh */}
                <ChooseColor handleOnChange={handleChangeForm} />

                <Button fullWidth variant="contained" type="submit" color="primary">
                    ng k
                </Button>

                <Typography variant="p" color="#fdd428" component="p" py={1}>
                    Bn  c ti khon
                </Typography>

                <Link to="/">
                    <Button fullWidth variant="contained" type="button" color="primary">
                        ng nhp
                    </Button>
                </Link>
            </Box>
        </form>
    );
}

export default FormRegister;
