import React from 'react';
import { Box, IconButton, Typography } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import Tippy from '@tippyjs/react';
import { followCursor } from 'tippy.js';
import { toast } from 'react-toastify';
import { useDispatch, useSelector } from 'react-redux';

import { selectorListSchedule, selectorListRoom, selectorUser } from '../../redux/selector';
import { actionsSchedule } from '../../components/Schedule/ScheduleSlice';
import 'tippy.js/dist/tippy.css';
import 'tippy.js/animations/shift-away.css';
import 'tippy.js/themes/light.css';
import { actionsTeacher } from '../../components/Teacher/TeacherSlice';

function ChooseLesson({ data, day, lesson, room }) {
    // kim tra tit ny c ngi ng k cha
    const hasLesson = Object.keys(data).length > 0;
    const dispatch = useDispatch();
    // Ly ti khon ang ng nhp
    const user = useSelector(selectorUser);
    const schedule = useSelector(selectorListSchedule);
    const listRooms = useSelector(selectorListRoom);
    // x l thm tit hc
    const handleAddLesson = () => {
        // Kim tra gio vin c ng k tit ny  phng no khc khng
        const roomHasLesson = listRooms.find((item) => {
            const crrLesson = schedule[item][day.id][lesson.id];
            let check;
            if (Object.keys(crrLesson).length > 0) {
                check = user.name === crrLesson.name;
            }
            return check;
        });
        // nu gio vin  ng k tit ny  phng khc th thng bo
        if (roomHasLesson) {
            toast.error(`Bn  ng k tit ny  phng ${roomHasLesson[roomHasLesson.length - 1]} `);
            return;
        } else {
            // Gi ln redux actions thm gio vin  ng k vo tit ny
            dispatch(
                actionsSchedule.addLesson({
                    room: room,
                    day: day.id,
                    lesson: lesson.id,
                    data: user,
                }),
            );
            // Thm tit hc vo ti khon gio vin
            dispatch(
                actionsTeacher.addLesson({
                    checkName: user.name,
                    data: {
                        room: room[room.length - 1],
                        day: day.name,
                        lesson: lesson.id[lesson.id.length - 1],
                    },
                }),
            );
            // Thng bo gio vin  ng k thnh cng
            toast.success(
                `Bn  ng k ${lesson.name[0].toLowerCase() + lesson.name.slice(1)} ca phng ${
                    room[room.length - 1]
                } vo ${day.name[0].toLowerCase() + day.name.slice(1)}`,
                {
                    autoClose: 2500,
                },
            );
        }
    };
    // X l xa tit hc
    const handleDeleteLesson = (e) => {
        // Ngn chn hnh vi ni bt ca js
        e.stopPropagation();
        // thng bo xa thnh cng
        toast.info(
            `Bn  hy ng k ${lesson.name[0].toLowerCase() + lesson.name.slice(1)} ca phng ${
                room[room.length - 1]
            } vo ${day.name[0].toLowerCase() + day.name.slice(1)}`,
            {
                autoClose: 2500,
            },
        );
        // Gi ln redux actions xa tit hc
        dispatch(
            actionsSchedule.deleteLesson({
                room: room,
                day: day.id,
                lesson: lesson.id,
            }),
        );

        dispatch(
            actionsTeacher.deleteLesson({
                checkName: user.name,
                data: {
                    room: room[room.length - 1],
                    day: day.name,
                    lesson: lesson.id[lesson.id.length - 1],
                },
            }),
        );
    };
    // Hin th tip
    const renderTooltip = (props) => {
        return (
            <Box borderRadius={2} tabIndex="-1" {...props}>
                <Typography variant="p" component="p">
                    {data.name}
                </Typography>
                <Typography variant="p" component="p">
                    {data.course}
                </Typography>
            </Box>
        );
    };
    return (
        <Box
            sx={{
                cursor: 'pointer',
                width: '100%',
                height: '32px',
                border: '1px solid #dee2e6',
                backgroundColor: '#fff',
                transition: 'all 0.2s linear',
                p: '2px',

                ':hover': {
                    backgroundColor: hasLesson ? '#fff' : '#cccccc66',
                },
            }}
            onClick={handleAddLesson}
        >
            {/* Kim tra nu c tit ny c gio vin ng k => hi th ra block vi mu gio vin chn */}
            {hasLesson && (
                <Tippy
                    // Hin th tip khi hover vo block
                    followCursor="horizontal"
                    plugins={[followCursor]}
                    arrow
                    animation="shift-away"
                    theme="material"
                    placement="top"
                    content={renderTooltip()}
                    moveTransition="all 0.1s linear"
                >
                    <Box
                        sx={{
                            backgroundColor: data.color,
                            width: '100%',
                            height: '100%',
                            borderRadius: '4px',
                            display: 'flex',
                            justifyContent: 'flex-end',
                            alignItems: 'center',
                            cursor: 'default',
                            ':hover .MuiIconButton-root': {
                                opacity: '1 !important',
                            },
                        }}
                        onClick={(e) => {
                            e.stopPropagation();
                        }}
                    >
                        {/* Nu tn trong thi kha biu ging tn ang ng nhp => cho php xa */}
                        {user.name === data.name && (
                            <IconButton
                                sx={{ p: 0, opacity: 0, transition: 'all 0.2s linear', cursor: 'pointer' }}
                                onClick={handleDeleteLesson}
                                color="error"
                            >
                                <DeleteIcon />
                            </IconButton>
                        )}
                    </Box>
                </Tippy>
            )}
        </Box>
    );
}

export default React.memo(ChooseLesson);
