import React, { useState } from 'react';
import { Box, Button, TextField } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';

import { selectorListTeacher, selectorNameTeachers } from '../../redux/selector';
import { actionsUser } from '../Account/AccountSlice';

function FormLogin() {
    const [inputValue, setInputValue] = useState('');
    // iu hng trang
    const navigate = useNavigate();
    // Ly tt c tn ca gio vin  ng k
    const nameTeachers = useSelector(selectorNameTeachers);
    // Ly tt c thng tin ca gio vin  ng k
    const listTeacher = useSelector(selectorListTeacher);
    const dispatch = useDispatch();
    // X l khi ngi dng bm ng nhp
    const handleLogin = (e) => {
        e.stopPropagation();
        // Kim tra ngi dng c nhp ng tn  ng k hay khng
        if (nameTeachers.includes(inputValue)) {
            toast.info('Bn  nhp thnh cng');
            const user = listTeacher.find((teacher) => teacher.name === inputValue);
            // Gi actions ln redux  thay i ti khon ang ng nhp
            dispatch(actionsUser.change(user));
            // Chuyn n trang thi kha biu
            navigate('/dashboard');
        } else {
            // thng bo li khi ngi dng nhp sai tn  ng k
            toast.error('Tn ng nhp khng ng vui lng kim tra li hoc ng k', { autoClose: 3000 });
        }
    };

    return (
        <Box mb={2} width="90%">
            <Box backgroundColor="#fff" borderRadius="4px" width="100%" mb={2}>
                {/*  nhp tn  */}
                <TextField
                    fullWidth
                    id="name-input"
                    name="name"
                    label="Name"
                    type="text"
                    value={inputValue}
                    onChange={(e) => setInputValue(e.target.value)}
                    color="success"
                />
            </Box>

            <Button
                onClick={handleLogin}
                sx={{
                    transition: 'all 0.4s linear',
                    color: '#fff',
                    background: 'linear-gradient( to right, #005bea, #00c6fb)',
                    fontSize: '16px',
                    fontWeight: 'bold',
                    '&:hover': { background: '#00C851' },
                }}
                variant="contained"
                type="button"
            >
                ng nhp
            </Button>
        </Box>
    );
}

export default FormLogin;
